.debug-panel {
  position: fixed;
  background: #111;
  color: #0ff;
  border-top: 4px solid #0ff6;
  padding: 1rem;
  z-index: 9999;
  max-width: 300px;
  display: none;
  transition: box-shadow 0.2s;
  top: 50%;
  left: 50%;

  h3 {
    margin: 0;
    margin-bottom: 1rem;
    font-size: 1rem;
    color: #0ff6;
  }

  .wrap {
    margin: .5rem;
  }

  &.active {
    display: block;
  }

  &.dragging {
    cursor: move;
    user-select: none;
  }

  .close-btn {
    position: absolute;
    right: 6px;
    top: 6px;
    font-size: 1.4rem;
    background: none;
    border: 1px solid #f88;
    color: #f88;
    cursor: pointer;
    transition: color 0.2s, transform 0.2s;
    border-radius: 50%;
    text-align: center;
    line-height: 14px;
    width: 20px;
    height: 20px;

    &:hover {
      color: red;
      transform: rotate(180deg);
    }
  }

  .debug-line {
    margin-bottom: 0.5rem;
    font-family: monospace;
  }
}
